.movies {
  display: flex;
  justify-content: space-between;
  /* 컨텐츠 박스 height 핏하게 (각 영역마다 다르게) */
  /* align-items: flex-start;  */
  flex-wrap: wrap;
}

.movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  color: #adaeb9;

  border-radius: 5px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 90, 0.25);
}

.movie,
.movie a {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  text-decoration: none;
  color: inherit;
}

.movie img {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
}

.movie .movie__title,
.movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie .movie__year,
.movie .movie__genres {
  opacity: 0.5;
  flex-wrap: wrap;
}

.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px;
}

.movie__genres li {
  margin-right: 10px;
  font-size: 14px;
}

/* movie detail */
.movie-detail {
  height: 100%;
  padding: 0 40px;
}

.movie-detail .movie {
  width: 100%;
}

@media (max-width: 950px) {
  #root .container {
    position: static;
    width: auto;
  }

  .movie {
    width: 100%;
  }

  .movie-detail .movie,
  .movie a {
    flex-direction: column;
    align-items: center;
  }

  .movie-detail .movie img,
  .movie a img {
    margin: 0;
  }
}
